(function () {
  'use strict';

  angular
  .module('utn')
  .directive('utnModalEditPrepareOrder', utnModalEditPrepareOrder);

  /* @ngInject */
  function utnModalEditPrepareOrder() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/inventory/prepare-order/modal-edit/modal-edit-prepare-order.html',
      controller: ModalEditPrepareOrderController,
      controllerAs: 'mEditPrepareOrder',
      bindToController: true,
      scope:{
        selectedPrepareOrder: '='
      }
    };

    return directive;

    /** @ngInject */
    function ModalEditPrepareOrderController(rPrepareOrder, rUbication, rProduct, SweetAlert, $rootScope, toastr, authenticationService) {
      var vm = this;
      vm.status = true;

      var userInfo = authenticationService.getUserInfo();
      if(userInfo.length > 0){
        vm.userConected = userInfo[0];
      }

      vm.prepareOrderEdited = {status:true};
      vm.editPrepareOrder = editPrepareOrder;
      vm.dismissModal = dismissModal;
      vm.searchProduct = searchProduct;
      vm.selectedItemDetail = selectedItemDetail;



      function init () {
        vm.prepareOrderEdited.status = true;
        loadProduct();
      }
      init();

      function prepareOrderToEdit(){
        if(vm.selectedPrepareOrder){
          rPrepareOrder.getInventoryProducts({id:vm.selectedPrepareOrder.id,ProductId: vm.selectedPrepareOrder.ProductId, WarehouseId: vm.selectedPrepareOrder.WarehouseId}, function(result){
            vm.prepareOrderEdited  = result;
            vm.prepareOrderEdited.typeDocument = vm.selectedPrepareOrder.documentSelected.TypeDocumentId;
            vm.prepareOrderEdited.productsDetailList = [];
            vm.ubicationList = [];
            angular.forEach(vm.prepareOrderEdited.records, function(item){
              vm.prepareOrderEdited.productsDetailList.push(item);
              vm.ubicationList.push(item.Ubication);
            });
          });
        }
      }

      function selectedItemDetail(item){
        vm.itemDetail = item;
        if (item.quantity > (vm.selectedPrepareOrder.quantity * -1)){
          vm.itemDetail.newQuantity = vm.selectedPrepareOrder.quantity * -1;
        }else{
          vm.itemDetail.newQuantity = item.quantity;
        }
      }

      function loadProduct(){
        rProduct.query(function(result){
          vm.productList  = result.records;
        });
      }

      function searchProduct(filter){
        if(filter.search == ''){
          loadProduct();
        }else{
          vm.query = {filter: filter.search};
          rProduct.query(vm.query, function (result) {
            vm.productList = result.records;
          });
        }
      }

      function editPrepareOrder(form){
        vm.loading = true;
        vm.submitAttempt = false;

        if(!form.$valid) {
          vm.loading = false;
          vm.submitAttempt = true;
          return;
        }

        if(vm.itemDetail.newQuantity > (vm.selectedPrepareOrder.quantity * -1)){
          toastr.error('La cantidad no puede ser mayor de la solicitada');
          vm.loading = false;
          return;
        }

        if(vm.itemDetail.quantity < vm.itemDetail.newQuantity){
          toastr.error('La cantidad no puede ser mayor a la Ubicacin');
          vm.loading = false;
          return;
        }

        vm.prepareOrderEdited.itemDetail = vm.itemDetail;
        vm.prepareOrderEdited.selectedPrepareOrder = vm.selectedPrepareOrder;
        vm.prepareOrderEdited.user = vm.userConected.user;


        rPrepareOrder.save(vm.prepareOrderEdited, function(){
          vm.loading = false;
          vm.submitAttempt = false;
          vm.itemDetail = {};
          vm.selectedPrepareOrder = {};
          toastr.success('Alisto realizado satisfactoriamente', {
            onShown: function(){
              angular.element('#modalEditPrepareOrder').modal('hide');
              $rootScope.$broadcast('prepareOrderEdited');
            }
          });
        },
        function(error){
          var errorMsg = '';
          var errorNo = 0;
          vm.loading = false;
          vm.submitAttempt = false;
          if(error.data.parent != undefined){
            toastr.error('Alisto realizado anteriormente. Verifque!', 'Error al realizar Alisto');
          }else{
            angular.forEach(error.data.errors, function(item){
              errorNo++;
              errorMsg += errorNo + '-' + item.message + '<br/>';
            })
            toastr.error("Ajuste(s):<br/>" + errorMsg, 'Error al relizar el Alisto');
          }
        });
      }

      function dismissModal (form){
        if(!form.$pristine) {
          SweetAlert.swal({
            title:'',
            text: 'Esta seguro de salir sin guardar?',
            showCancelButton: true,
            confirmButtonText:'S',
            cancelButtonText:'No',
            closeOnConfirm: true,
            closeOnCancel: true,
            allowEscapeKey: false
          },
          function(isConfirm){
            if (isConfirm) {
              angular.element('#modalEditPrepareOrder').modal('hide');
              form.$setPristine();
              vm.itemDetail = {};
            }
          }
          );
        }else{
          angular.element('#modalEditPrepareOrder').modal('hide');
          form.$setPristine();
          vm.itemDetail = {};
          vm.selectedPrepareOrder = {};
        }
      }

      angular.element('#modalEditPrepareOrder').on('shown.bs.modal', function(){
        prepareOrderToEdit();
      });

      angular.element('#modalEditPrepareOrder').on('show.bs.modal', function () {
        vm.loading = false;
        vm.submitAttempt = false;
        vm.addAttempt = false;
        vm.prepareOrderToEdit = {};
        vm.prepareOrderEdited = {};
        init();
      });
    }
  }
})();

(function () {
  'use strict';

  angular
  .module('utn')
  .directive('utnModalCancelPrepareOrder', utnModalCancelPrepareOrder);

  /* @ngInject */
  function utnModalCancelPrepareOrder() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/inventory/prepare-order/modal-cancel/modal-cancel-prepare-order.html',
      controller: ModalCancelPrepareOrderController,
      controllerAs: 'mCancelPrepareOrder',
      bindToController: true,
      scope:{
        selectedPrepareOrder: '='
      }
    };

    return directive;

    /** @ngInject */
    function ModalCancelPrepareOrderController(rPrepareOrder, rUbication, rProduct, SweetAlert, $rootScope, toastr, authenticationService) {
      var vm = this;
      vm.status = true;

      var userInfo = authenticationService.getUserInfo();
      if(userInfo.length > 0){
        vm.userConected = userInfo[0];
      }

      vm.prepareOrderCanceled = {status:true};
      vm.cancelPrepareOrder = cancelPrepareOrder;
      vm.dismissModal = dismissModal;
      vm.searchProduct = searchProduct;



      function init () {
        vm.prepareOrderCanceled.status = true;
        loadProduct();
      }
      init();

      function prepareOrderToCancel(){
        if(vm.selectedPrepareOrder){
          vm.prepareOrderCanceled = vm.selectedPrepareOrder; 
        }
      }

      function loadProduct(){
        rProduct.query(function(result){
          vm.productList  = result.records;
        });
      }

      function searchProduct(filter){
        if(filter.search == ''){
          loadProduct();
        }else{
          vm.query = {filter: filter.search};
          rProduct.query(vm.query, function (result) {
            vm.productList = result.records;
          });
        }
      }

      function cancelPrepareOrder(form){
        vm.loading = true;
        vm.submitAttempt = false;

        if(!form.$valid) {
          vm.loading = false;
          vm.submitAttempt = true;
          return;
        }


        rPrepareOrder.cancelPrepareOrder(vm.prepareOrderCanceled, function(){
          vm.loading = false;
          vm.submitAttempt = false;
          vm.prepareOrderCanceled = {};
          vm.selectedPrepareOrder = {};
          toastr.success('Alisto aprovado satisfactoriamente', {
            onShown: function(){
              angular.element('#modalCancelPrepareOrder').modal('hide');
              $rootScope.$broadcast('prepareOrderEdited');
            }
          });
        },
        function(error){
          var errorMsg = '';
          var errorNo = 0;
          vm.loading = false;
          vm.submitAttempt = false;
          if(error.data.parent != undefined){
            toastr.error('Alisto aprovado anteriormente. Verifque!', 'Error al realizar Alisto');
          }else{
            if(error.data.errors != undefined){
              angular.forEach(error.data.errors, function(item){
                errorNo++;
                errorMsg += errorNo + '-' + item.message + '<br/>';
              })
              toastr.error("Ajuste(s):<br/>" + errorMsg, 'Error al aprovado el Alisto');
              }
              else{
                toastr.error("Cdigo Incorrecto");
              }
          }
        });
      }

      function dismissModal (form){
        if(!form.$pristine) {
          SweetAlert.swal({
            title:'',
            text: 'Esta seguro de salir sin guardar?',
            showCancelButton: true,
            confirmButtonText:'S',
            cancelButtonText:'No',
            closeOnConfirm: true,
            closeOnCancel: true,
            allowEscapeKey: false
          },
          function(isConfirm){
            if (isConfirm) {
              angular.element('#modalCancelPrepareOrder').modal('hide');
              form.$setPristine();
              vm.prepareOrderCanceled = {};
            }
          }
          );
        }else{
          angular.element('#modalCancelPrepareOrder').modal('hide');
          form.$setPristine();
          vm.prepareOrderCanceled = {};
          vm.selectedPrepareOrder = {};
        }
      }

      angular.element('#modalCancelPrepareOrder').on('shown.bs.modal', function(){
        prepareOrderToCancel();
      });

      angular.element('#modalCancelPrepareOrder').on('show.bs.modal', function () {
        vm.loading = false;
        vm.submitAttempt = false;
        vm.addAttempt = false;
        vm.prepareOrderToCancel = {};
        vm.prepareOrderCanceled = {};
        init();
      });
    }
  }
})();

(function () {
  'use strict';

  angular
  .module('utn')
  .directive('utnModalEditDocument', utnModalEditDocument);

  /* @ngInject */
  function utnModalEditDocument() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/inventory/document/modal-edit/modal-edit-document.html',
      controller: ModalEditDocumentController,
      controllerAs: 'mEditDocument',
      bindToController: true,
      scope:{
        selectedItem: '='
      }

    };

    return directive;

    /** @ngInject */
    function ModalEditDocumentController(rDocument, rProduct, rTypeDocument, SweetAlert, $rootScope, toastr, authenticationService, moment, DATE_ISO_FORMAT, utilsService) {
      var vm = this;
      vm.status = true;
      vm.documentEdited = {status:true};

      var userInfo = authenticationService.getUserInfo();
      if(userInfo.length > 0){
        vm.userConected = userInfo[0];
      }

      vm.dateUI = new Date();
      vm.documentEdited = {};
      vm.documentEdited.productsDetailList = [];

      vm.editDocument = editDocument;
      vm.dismissModal = dismissModal;
      vm.searchProduct = searchProduct;

      vm.editDetail = editDetail;
      vm.addDetail = addDetail;
      vm.deleteDetail = deleteDetail;
      vm.cleanFormDetail = cleanFormDetail;
      vm.detailSelectedIndex = -1;

      function init () {
        vm.documentEdited.status = true;
        loadProduct();
        loadTypeDocument();
      }
      init();

      function documentToEdit(){
        if(vm.selectedItem){

          vm.showMe = false;
          if(vm.selectedItem.showMe){
            vm.showMe = true;
          }

          rDocument.query({id:vm.selectedItem.id}, function(result){
            vm.documentEdited  = result;

            vm.documentEdited.status = (vm.documentEdited.status == 1)?true:false;

            vm.warehouse = {};
            vm.warehouse.name = vm.selectedItem.warehouseName;

            vm.documentEdited.productsDetailList = [];

            vm.dateUI = moment(vm.documentEdited.date, DATE_ISO_FORMAT).toDate();

            // if(vm.documentEdited.TypeDocumentId){
            //   var action = parseInt(vm.documentEdited.TypeDocumentId);
            //   switch (action) {
            //     case 1:
            //       vm.documentEdited.typeDocumentDup = vm.typeDocumentList[0];
            //       break;
            //     case 2:
            //       vm.documentEdited.typeDocumentDup = vm.typeDocumentList[1];
            //       break;
            //     case 3:
            //       vm.documentEdited.typeDocumentDup = vm.typeDocumentList[2];
            //       break;
            //     case 4:
            //       vm.documentEdited.typeDocumentDup = vm.typeDocumentList[3];
            //       break;
            //     case 5:
            //       vm.documentEdited.typeDocumentDup = vm.typeDocumentList[4];
            //       break;
            //     case 6:
            //       vm.documentEdited.typeDocumentDup = vm.typeDocumentList[5];
            //       break;
            //     case 7:
            //       vm.documentEdited.typeDocumentDup = vm.typeDocumentList[6];
            //       break;
            //     case 8:
            //       vm.documentEdited.typeDocumentDup = vm.typeDocumentList[7];
            //       break;
            //   }
            // }

            angular.forEach(vm.documentEdited.Products, function(item){
              var obj = {};
              obj.product = {id:item.id, code:item.code, name:item.name};
              obj.quantity = item.Document_Product_List.quantity;
              vm.documentEdited.warehouseId = item.Document_Product_List.WarehouseId;
              vm.documentEdited.productsDetailList.push(obj);
            });
          });
        }
      }

      function loadTypeDocument(){
        vm.query = {direction:'ASC'};
        rTypeDocument.query(vm.query, function(result){
          vm.typeDocumentList  = result.records;
        });
      }

      function loadProduct(){
        rProduct.query(function(result){
          vm.productList  = result.records;
        });
      }

      function searchProduct(filter){
        if(filter.search == ''){
          loadProduct();
        }else{
          vm.query = {filter: filter.search};
          rProduct.query(vm.query, function (result) {
            vm.productList = result.records;
          });
        }
      }

      function cleanFormDetail() {
        vm.addAttempt = false;
        vm.detailSelectedIndex = -1;
        vm.itemDetail = { amount: 0 };
      }

      function editDetail(index) {
        vm.detailSelectedIndex = index;
        vm.itemDetail = {};

        angular.copy(vm.documentEdited.productsDetailList[vm.detailSelectedIndex], vm.itemDetail);
      }


      function addDetail () {
        var detailToAdd = {};
        vm.addAttempt = true;
        vm.duplicate = false;

        if (vm.itemDetail.product && vm.itemDetail.quantity){
          angular.copy(vm.itemDetail, detailToAdd);

          if (vm.detailSelectedIndex === -1) {
            var arr = ['product'];
            if (!utilsService.validateDuplicateList(vm.itemDetail,vm.documentEdited.productsDetailList, arr)) {
              detailToAdd.quantity = parseInt(detailToAdd.quantity);
              vm.documentEdited.productsDetailList.push(detailToAdd);
              vm.addAttempt = false;
            }else{
              angular.forEach(vm.documentEdited.productsDetailList, function(item){
                if(item.product.id == detailToAdd.product.id){
                  item.quantity += parseInt(detailToAdd.quantity);
                }
              });
              vm.addAttempt = false;
            }
          }else{
            detailToAdd.quantity = parseInt(detailToAdd.quantity);
            vm.documentEdited.productsDetailList[vm.detailSelectedIndex] = detailToAdd;
            vm.addAttempt = false;
          }
        }else{
          vm.addAttempt = true;
        }

        vm.detailSelectedIndex = -1;
        vm.itemDetail = { amount: 0 };
        //cleanFormDetail();
      }

      function deleteDetail (index) {
        SweetAlert.swal({
            title:'',
            text: 'Desea eliminar el registro de la lista?',
            showCancelButton: true,
            confirmButtonText:'S',
            cancelButtonText:'No',
            closeOnConfirm: true,
            closeOnCancel: true,
            allowEscapeKey: false
          },
          function(isConfirm){
            if (isConfirm) {
              vm.documentEdited.productsDetailList.splice(index, 1);
            }
          }
        );
      }

      function editDocument(form){
        vm.loading = true;
        vm.submitAttempt = false;

        if(!form.$valid) {
          vm.loading = false;
          vm.submitAttempt = true;
          return;
        }

        if(vm.documentEdited.productsDetailList.length == 0 &&
          (vm.documentEdited.TypeDocumentId == 6 || vm.documentEdited.TypeDocumentId == 4)){
          if(vm.showMe){
            toastr.error('El Documento debe contener al menos un Producto asociado');
          }else{
            toastr.error('La Devolucin debe contener al menos un Producto asociado');
          }
          vm.loading = false;
          return;
        }

        vm.documentEdited.ids = [];
        vm.documentEdited.quantitys = [];
        angular.forEach(vm.documentEdited.productsDetailList, function(item){
          vm.documentEdited.ids.push(item.product.id);
          vm.documentEdited.quantitys.push(item.quantity);
        })

        rDocument.update(vm.documentEdited, function(){
          vm.loading = false;
          vm.submitAttempt = false;
          if(vm.showMe){
            toastr.success('Recibo actualizado satisfactoriamente', {
              onShown: function(){
                angular.element('#modalEditDocument').modal('hide');
                $rootScope.$broadcast('documentEdited');
              }
            });
          }else{
            toastr.success('Devolucin actualizada satisfactoriamente', {
              onShown: function(){
                angular.element('#modalEditDocument').modal('hide');
                $rootScope.$broadcast('returnEdited');
              }
            });
          }
        },
        function(error){
          var errorMsg = '';
          var errorNo = 0;
          vm.loading = false;
          vm.submitAttempt = false;
          angular.forEach(error.data.errors, function(item){
            errorNo++;
            errorMsg += errorNo + '-' + item.message + '<br/>';
          })
          if(vm.showMe){
            toastr.error("Ajuste(s):<br/>" + errorMsg, 'Error al editar Recibo');
          }else{
            toastr.error("Ajuste(s):<br/>" + errorMsg, 'Error al editar Devolucin');
          }
        });
      }

      function dismissModal (form){
        if(!form.$pristine) {
          SweetAlert.swal({
            title:'',
            text: 'Esta seguro de salir sin guardar?',
            showCancelButton: true,
            confirmButtonText:'S',
            cancelButtonText:'No',
            closeOnConfirm: true,
            closeOnCancel: true,
            allowEscapeKey: false
          },
          function(isConfirm){
            if (isConfirm) {
              angular.element('#modalEditDocument').modal('hide');
              form.$setPristine();
            }
          }
          );
        }else{
          angular.element('#modalEditDocument').modal('hide');
          form.$setPristine();
        }
      }

      angular.element('#modalEditDocument').on('shown.bs.modal', function(){
        documentToEdit();
      });

      angular.element('#modalEditDocument').on('show.bs.modal', function () {
        vm.loading = false;
        vm.submitAttempt = false;
        vm.addAttempt = false;
        vm.itemDetail = {};
        vm.documentEdited = {};
        vm.documentEdited.productsDetailList = [];

        init();
      });
    }
  }
})();

